  * {
    border-color: var(--border);
    outline-color: color-mix(in oklab, var(--ring) 50%, transparent);
  }
}
::selection {
  background: #333;
  color: #fffc00;
}
::-moz-selection {
  background: #333;
  color: #fffc00;
}
a {
  outline: none;
}
html {
  background-color: #fffc00;
  background: #fffc00;
  background: linear-gradient(-45deg, #fff, #fff, #fffc00, #fffc00);
  background-size: 400% 400%;
  animation: gradientBg 8s ease-in infinite;
  animation-delay: 7s;
  color: #1e1e1e;
  transition: transform 0.3s ease-out;
}
body:after {
  content: "";
  position: fixed;
  z-index: 5;
  top: 0;
  left: 0;
  right: 0;
  height: 5px;
  background-color: rgb(225, 240, 82);
  transition: opacity 1s ease-out;
}
.footer {
  position: fixed;
  bottom: 20px;
  right: 30px;
  font-size: 0.8em;
  opacity: 0.85;
  opacity: 0;
  z-index: 6;
  animation: slideIn 1.2s cubic-bezier(0.68, -0.55, 0.265, 1.55) 1s forwards;
  transition: opacity 1.3s ease-out, transform 0.3s ease-out;
}
.footer .glyph {
  margin: 0 5px;
}
.footer .avatar {
  position: absolute;
  right: -60px;
  bottom: -20px;
  transform: scale(0.8) rotate(-45deg);
  transition: transform 0.15s ease-out;
}
.footer svg {
  vertical-align: middle;
  height: 1.35em;
}
a.footer {
  vertical-align: middle;
  text-decoration: none;
  color: #1e1e1e;
}
.fadeIn {
  opacity: 0;
  animation: fadeIn 1s ease-out 1s;
  animation-fill-mode: forwards;
}
.fadeOut {
  animation: fadeOut 1s ease-out;
  animation-fill-mode: forwards;
}
.background {
  pointer-events: none;
  animation: buildInBg 2s ease-in 0.35s;
  animation-fill-mode: forwards;
  background: url("/images/bg.png") no-repeat 50% 100%;
  background-size: cover;
  position: absolute;
  width: 135%;
  left: -5% !important;
  top: -5% !important;
  bottom: -5% !important;
  opacity: 0;
  z-index: -2;
}
